<template>
    <div class="login-container">
        <!-- 登录表单区域 -->
        <div class="login-box">
            <h2 style="margin-top: -15px; color: #fff;">山建智慧能源装备与系统平台</h2>
            <el-form :model="loginForm" label-position="top">
                <div class="input-item">
                    <el-input v-model="loginForm.username" placeholder="请输入用户名" />
                </div>
                <div class="input-item">
                    <el-input v-model="loginForm.password" type="password" placeholder="请输入密码" show-password />
                </div>
                <el-button type="primary" native-type="submit" style="width: 100%;" @click="handleLogin">登录</el-button>
            </el-form>
        </div>
    </div>
</template>

<script>
import { userLogin } from '@/api/login';

export default {
    data() {
        return {
            loginForm: {
                username: '',
                password: ''
            }
        };
    },
    methods: {
        handleLogin() {
            // 验证账号密码不能为空
            if (!this.loginForm.username) {
                this.$message.error('用户名不能为空');
                return;
            }

            if (!this.loginForm.password) {
                this.$message.error('密码不能为空');
                return;
            }

            // 调用login方法进行登录
            userLogin(this.loginForm).then(res => {
                console.log('登录响应:', res);
                
                // 保存用户信息到localStorage
                if (res.data) {
                    localStorage.setItem('user', JSON.stringify(res.data));
                }

                this.$message.success('登录成功');
                // 登录成功后跳转到build1页面，并传递用户ID
                this.$router.push({
                    path: '/build1',
                    query: {
                        userId: res.data?.id || res.data?.userId || 0
                    }
                });
            }).catch(error => {
                // 登录失败的处理
                console.error('登录失败:', error);
                this.$message.error(error.message || '登录失败');
            });
        }
    }
};
</script>

<style scoped>
.login-container {
    width: 100%;
    height: 100vh;
    background-image: url('@/assets/背景.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-box {
    background-color: #20416D;
    padding: 30px;
    border-radius: 8px;
    box-shadow: 0 4px 12px #fff;
    width: 400px;
}

.input-item {
    margin-bottom: 20px;
}
</style>