<template>
  <el-menu :default-active="activeIndex2" class="el-menu-demo" mode="horizontal" @select="handleSelect"
    background-color="#0a1929" text-color="#e0e0ff" active-text-color="#4dabf7"
    style="width: 100%; display: flex; justify-content: space-between; border-bottom: 1px solid #1a365d;">
    <!-- 添加新的mini商标图片 -->
    <div class="mini-logo-container">
      <img src="@/assets/mini商标.png" alt="mini商标" class="mini-logo-image" />
    </div>
    <el-menu-item index="0"
      style="pointer-events: none; font-size: 24px; color: #fff; flex: 1; text-align: center; font-weight: bold;">
      智慧能源装备与系统平台
    </el-menu-item>

    <el-submenu index="1" style="flex: 1; text-align: center;">
      <template slot="title">
        <i class="el-icon-house" style="margin-right: 5px;"></i>首页
      </template>
      <el-menu-item v-for="(building, index) in buildings" :key="index" :index="'1-' + (index + 1)"
        @click="goToBuilding(building.id)">
        {{ building.name }}
      </el-menu-item>
    </el-submenu>

    <el-menu-item index="2" style="flex: 1; text-align: center;" @click="$router.push({ name: 'zhongyangkongtiao' })">
      <i class="el-icon-setting" style="margin-right: 5px;"></i>工作台
    </el-menu-item>

    <el-menu-item index="3" style="flex: 1; text-align: center;" @click="$router.push({ name: 'shujuzonglan' })">
      <i class="el-icon-data-analysis" style="margin-right: 5px;"></i>能耗监测
    </el-menu-item>

    <el-menu-item index="4" style="flex: 1; text-align: center;" @click="$router.push({ name: 'shipinjiankong' })">
      <i class="el-icon-video-camera" style="margin-right: 5px;"></i>监控中心
    </el-menu-item>

    <el-menu-item index="5" style="flex: 1; text-align: center;" @click="$router.push({ name: 'gaojingjibie' })">
      <i class="el-icon-bell" style="margin-right: 5px;"></i>告警中心
    </el-menu-item>

    <el-menu-item index="6" style="flex: 1; text-align: center;" @click="$router.push({ name: 'zhinengkongzhi' })">
      <i class="el-icon-monitor" style="margin-right: 5px;"></i>控制中心
    </el-menu-item>
    
    <!-- 新增的三个功能按钮 -->
    <el-menu-item index="7" style="flex: 1; text-align: center;" @click="$router.push({ name: 'nengtanfenxi' })">
      <i class="el-icon-data-line" style="margin-right: 5px;"></i>能碳分析
    </el-menu-item>
    
    <el-menu-item index="8" style="flex: 1; text-align: center;" @click="$router.push({ name: 'guangfufadian' })">
      <i class="el-icon-sunny" style="margin-right: 5px;"></i>光伏发电
    </el-menu-item>
    
    <el-menu-item index="9" style="flex: 1; text-align: center;" @click="$router.push({ name: 'chongdianzhuangguanli' })">
      <i class="el-icon-charging" style="margin-right: 5px;"></i>充电桩管理
    </el-menu-item>
    
    <el-menu-item index="10" style="flex: 1; text-align: center;" @click="$router.push({ name: 'gongdanchuli' })">
      <i class="el-icon-document" style="margin-right: 5px;"></i>工单流转
    </el-menu-item>
  </el-menu>
</template>

<script>
export default {
  data() {
    return {
      activeIndex: '1',
      activeIndex2: '1',
      userId: null,
      buildings: []
    };
  },
  mounted() {
    // 从localStorage获取用户信息
    this.loadUserInfo();
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    },
    loadUserInfo() {
      try {
        const user = JSON.parse(localStorage.getItem('user'));
        if (user) {
          // 获取用户ID
          this.userId = user.id || user.userId || null;

          // 获取用户有权访问的楼宇列表
          this.buildings = user.buildings || [
            { id: 1, name: '楼宇A' },
            { id: 2, name: '楼宇B' },
            { id: 3, name: '楼宇C' }
          ];
        } else {
          // 默认值，用于测试
          this.buildings = [
            { id: 1, name: '楼宇A' },
            { id: 2, name: '楼宇B' },
            { id: 3, name: '楼宇C' }
          ];
        }
      } catch (e) {
        console.error('解析用户信息失败', e);
        // 出错时使用默认值
        this.buildings = [
          { id: 1, name: '楼宇A' },
          { id: 2, name: '楼宇B' },
          { id: 3, name: '楼宇C' }
        ];
      }
    },
    goToBuilding(buildingId) {
      // 检查是否已经在这个页面上，避免重复导航错误
      const currentRoute = this.$route;
      if (currentRoute.name === 'Build1' &&
        currentRoute.query.buildingId == buildingId) {
        // 如果已在相同路由，只更新buildingId参数
        if (currentRoute.query.buildingId != buildingId) {
          this.$router.replace({
            name: 'Build1',
            query: {
              buildingId: buildingId,
              userId: this.userId
            }
          }).catch(err => {
            if (err.name !== 'NavigationDuplicated') {
              throw err;
            }
          });
        }
        return;
      }

      // 跳转到楼宇页面，并传递楼宇ID和用户ID
      this.$router.push({
        name: 'Build1',
        query: {
          buildingId: buildingId,
          userId: this.userId
        }
      }).catch(err => {
        // 捕获并处理导航错误
        if (err.name !== 'NavigationDuplicated') {
          throw err;
        }
      });
    }
  }
}
</script>

<style scoped>
.el-menu {
  width: 100%;
  display: flex;
  justify-content: space-between;
  box-shadow: 0 2px 12px 0 rgba(0, 20, 40, 0.3);
}

/* mini商标图片容器样式 */
.mini-logo-container {
  display: flex;
  align-items: center;
  padding-left: 10px;
  height: 100%;
}

.mini-logo-image {
  height: 45px;
  width: auto;
  position: relative;
  top: 5px;
  /* 多浏览器兼容的图片渲染优化 */
  image-rendering: -webkit-optimize-contrast;
  /* Safari */
  image-rendering: -moz-crisp-edges;
  /* Firefox */
  image-rendering: -o-crisp-edges;
  /* Opera */
  image-rendering: crisp-edges;
  /* 标准属性 */
  -ms-interpolation-mode: bicubic;
  /* IE */

  -webkit-user-drag: none;
  max-width: 100%;
}

.el-menu-item,
.el-submenu {
  flex: 1;
  text-align: center;
  transition: all 0.3s ease;
}

.el-menu-item:hover,
.el-submenu:hover {
  background-color: #1a365d !important;
  color: #63b3ed !important;
}

.el-menu-item.is-active {
  border-bottom: 2px solid #4dabf7 !important;
  background-color: #1a365d !important;
}

.el-submenu .el-submenu__title:hover {
  background-color: #1a365d !important;
  color: #63b3ed !important;
}

.el-menu--horizontal .el-submenu .el-submenu__icon-arrow {
  color: #e0e0ff;
}
</style>